.profileSection {
   padding-top: 15px;
   height: auto;

   .jumbotron {
      font-size: 4rem;
      background-image: linear-gradient(
         to bottom,
         rgba(165, 24, 24, 0.6) 0%,
         rgba(255, 255, 255, 1) 100%
      );
   }

   .portrait {
      padding-top: 2rem;
   }
   img {
      margin-right: 0;
      margin-left: 0rem;
      opacity: 0.8;
      max-height: 500px;
      max-width: 70%;
      border-radius: 2%;
      border-style: solid;
   }

   h2 {
      margin-top: 1.5rem;
   }
   p {
      padding-top: 2rem;
      padding-left: 0;
      margin-left: 6rem;
      margin-right: 6rem;
      margin-bottom: 5rem;
      font-size: 28px;
      // text-shadow: black 0.3em 0.3em 0.3em;
   }
}

.button {
   background-color: rgb(77, 73, 73);
   border-color: rgb(77, 73, 73);
}

a::hover {
  background-color: bisque;
 transform: scale(3);
}
button::hover {
   transform: scale(1.1)!important;

}
.social {
   padding-top: 10px;
   align-items: center;
   justify-content: center;
   a {
      color: black !important;
   }
}
@media screen and (max-width: 746px) {
   .profileSection {
      flex-wrap: inherit;

      p {
         padding-top: 1rem;
         padding-left: 0;
         margin-left: 1rem;
         margin-right: 1rem;
         margin-bottom: 3rem;
         font-size: 25px;
      }
   }
}

