$main-light-blue: rgba(152, 220, 236, 0.8);

@-webkit-keyframes drop {
   0% {
      top: -50%;
   }
   100% {
      top: 110%;
   }
}

@keyframes drop {
   0% {
      top: -50%;
   }
   100% {
      top: 110%;
   }
}
/* The element to apply the animation to */

.skillsSection {
   justify-content: center;
   background-color: rgba(223, 196, 196, 0.13);
   border-radius: 2%;
   h5 {
      font-size: 20px;
   }
   .card {
      display: flex;
   }
}

.skillCard {
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
   display: flex;
   height: 12rem;
   box-shadow: 0 4px 21px -12px rgba(0, 0, 0, 0.66);
   border-radius: 10px;
   margin: 0 0 2rem 0;
   overflow: hidden;
   position: relative;
   color: #000000;
   cursor: pointer;
   width: auto;
   npm s &.dark {
      background-color: #18151f;
   }
   &.light {
      background-color: #0053b8;
   }

   .t-dark {
      color: #18151f;
   }

   a {
      color: inherit;
   }

   h1,
   .h1 {
      margin-bottom: 0.5rem;
      font-weight: 500;
      line-height: 1.2;
   }

   .small {
      font-size: 80%;
   }

   .postcard__title {
      font-size: 1.75rem;
   }

   .postcard__img {
      max-height: 180px;
      width: 100%;
      object-fit: cover;
      position: relative;
   }

   .postcard__img_link {
      display: contents;
   }

   .postcard__bar {
      width: 50px;
      height: 10px;
      margin: 10px 0;
      border-radius: 5px;
      background-color: #424242;
      transition: width 0.2s ease;
   }

   .postcard__text {
      font-family: "Baloo 2", cursive;
      padding: 1.5rem;
      position: relative;
      display: flex;
      flex-direction: column;
   }

   .postcard__preview-txt {
      overflow: hidden;
      text-overflow: ellipsis;
      text-align: justify;
      height: 100%;
   }

   .postcard__tagbox {
      display: flex;
      flex-flow: row wrap;
      font-size: 14px;
      margin: 20px 0 0 0;
      padding: 0;
      justify-content: center;

      .tag__item {
         display: inline-block;
         background: rgba(83, 83, 83, 0.4);
         border-radius: 3px;
         padding: 2.5px 10px;
         margin: 0 5px 5px 0;
         cursor: default;
         user-select: none;
         transition: background-color 0.3s;

         &:hover {
            background: rgba(83, 83, 83, 0.8);
         }
      }
   }
   .postcard__codelinks {
      display: flex;
      flex-flow: row wrap;
      font-size: 14px;
      margin: 20px 0 0 0;
      padding: 0;
      justify-content: center;

      .tag__item {
         display: inline-block;
         background: rgba(83, 83, 83, 0.4);
         border-radius: 3px;
         padding: 2.5px 10px;
         margin: 0px 2px 0px 0px;
         cursor: default;
         user-select: none;
         transition: background-color 0.3s;

         &:hover {
            background: $main-light-blue;
         }
      }
   }
}

.skillCard:hover {
    
      transform: scale(1.1);
    
}
